* {
  box-sizing: border-box;
}

html {
  font-size: 20px;
}

body {
  width: auto;
  max-width: 600px;
  margin: auto;
  padding: 20px;
}

input {
  border-radius: 5px;
  width: 100%;
  margin: 3px 0;
  padding: 5px;
  font-family: monospace;
  font-size: 20px;
}

.form-line {
  flex-wrap: wrap;
  gap: 0 5px;
  display: flex;
}

.flex-sb {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.form-line > * {
  flex-grow: 1;
  width: auto;
}

.game-field {
  flex-direction: row;
  margin-bottom: 40px;
  display: flex;
}

.game-row {
  flex-direction: column-reverse;
  justify-content: flex-end;
  display: flex;
}

.game-item {
  color: #0000;
  border: 1px solid #444;
  border-radius: 7px;
  width: 50px;
  height: 50px;
  margin: 1px;
  transition: all .3s ease-out;
}

.game-item._empty {
  background-color: #eee;
  border-color: #0000;
}

.game-item._x {
  background-color: #3030b0;
}

.game-item._o {
  background-color: #b03030;
}

.game-item._X {
  background-color: #1010f0;
}

.game-item._O {
  background-color: #f01010;
}

.game-item._X, .game-item._O {
  animation: .4s ease-in-out infinite alternate animate-game-win-item;
  box-shadow: inset 0 0 4px 4px #ffff00e6;
}

.game-row._active .game-item._empty {
  background-color: #ff8;
  border-color: #bbb;
}

.game-item._x, .game-item._o {
  animation: .5s linear animate-game-item;
}

@keyframes animate-game-item {
  from {
    transform: translateY(-300px);
  }

  to {
    transform: translateY(0);
  }
}

@keyframes animate-game-win-item {
  from {
    box-shadow: inset 0 0 10px 2px #ff0;
  }

  to {
    box-shadow: inset 0 0 4px 4px #ff0;
  }
}

.player-name {
  border-radius: 8px;
  padding: 7px;
}

.player-name._empty {
  color: #fff;
}

.player-name._empty:after {
  content: "...awaiting...";
}

.player-name._x {
  background-color: #c0c0ff;
}

.player-name._o {
  background-color: #ffc0c0;
}

.invite-info {
  padding-top: 100px;
}

.invite-image {
  height: unset !important;
  width: 100% !important;
}

